.lang {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .lang.media767 {
    display: none;
  }
}

.lang__item {
  display: block;
  color: #344c57;
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.lang__item:not(:first-child) {
  margin-left: 38px;
}

.lang__item.is-active .lang__text::after {
  transform: scaleX(0);
}

.lang__item.is-active .lang__text::before {
  transform: scale(1);
}

.lang__item:last-child .lang__text::after {
  left: auto;
  right: 6px;
}

.lang__text {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 50%;
  cursor: pointer;
  width: 40px;
  height: 40px;
  text-decoration: none;
}

.lang__text::before {
  position: absolute;
  content: '';
  width: 40px;
  height: 40px;
  border-radius: 50%;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: -1;
  transform: scale(0);
  transition: transform 0.3s;
}

.is-hover.lang .lang__text::before {
  transform: scale(0);
}

.lang_green.lang .lang__text::before {
  background-color: #51e3c2;
}

.lang__text::after {
  position: absolute;
  content: '';
  width: 59px;
  height: 1px;
  top: 50%;
  left: 10%;
  background-color: #373e41;
  transition: transform 0.3s;
}

.is-hover.lang .lang__text::after {
  transform: scale(1);
}

.lang__text:hover::after {
  transform: scaleX(0);
}

.is-hover.lang .lang__text:hover::after {
  transform: scaleX(0);
}

