.footer {
	padding: 55px 0 70px;
	background: #fafafa;
	@include xs {
		padding: 50px 0; }
	p {
		line-height: 30px;
		@include xs {
			line-height: 16px;
			font-size: 12px; } }
	&__logo {
		margin-bottom: 70px;
		@include r(1199) {
			margin-bottom: 200px; }
		@include xs {
			display: none; } }
	&__top {
		display: flex;
		justify-content: space-between;
		@include xs {
			display: block; } }
	&__icon {
		width: 195px;
		flex: none;
		@include r(1199) {
			width: 250px; }
		@include xs {
			margin-bottom: 35px; } }
	&__info {
		display: flex;
		flex-wrap: wrap; }
	&__col {
		width: 195px;
		@include r(1199) {
			width: 50%; }
		&_hide {
			@include xs {
				display: none; } }
		&_xs {
			@include xs {
				width: 50%; } }
		&_md {
			@include xs {
				width: 100%;
				order: -1;
				display: flex;
				justify-content: space-between; } } }

	&__subtitle {
		margin-bottom: 4px;
		line-height: 40px; }
	&__row {
		margin-bottom: 18px;
		&_hide {
			display: block;
			margin-bottom: 0;
			@include xs {
				display: none; } }
		&_show {
			display: none;
			@include xs {
				display: block; } }
		&_xs {
			@include xs {
				width: 50%;
				margin-bottom: 32px; } } }
	&__soc-icons {
		display: flex;
		justify-content: space-between;
		max-width: 140px;
		margin-top: 10px; }
	&__carts {
		display: flex;
		justify-content: space-between;
		max-width: 175px;
		padding-top: 13px; }
	&__nav {
		font-size: 10px;
		font-weight: 600;
		text-transform: uppercase;
		li {
			line-height: 30px; } }
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center; } }

